			})
		},
		// 提示开通会员
		promptOpenVip(callBack) {
			uni.showModal({
				title: '系统提示',
				content: '系统检测到用户未开通会员，是否开通会员',
				success: res => {
					if (res.confirm) {
						// console.log('用户点击确定');
						// const that = this;
						// const id = activity.get_data_set(e, "id");
						index.navigate_to(`/pages/vip/vip`);
